"use client";
import React, { useEffect } from "react";
import { ComponentRegistry, type FormSchema } from "@shadcn-builder/renderer";
import { useDesignerStore } from "./state/store";
import { LeftPanel } from "./panels/LeftPanel";
import { RightPanel } from "./panels/RightPanel";
import { Canvas } from "./Canvas";

export function Designer({ value, defaultValue, onChange, registry, viewport = "sm", showJSON }: {
  value?: FormSchema;
  defaultValue?: FormSchema;
  onChange?: (next: FormSchema) => void;
  registry: ComponentRegistry;
  viewport?: "sm" | "md" | "lg";
  showJSON?: boolean;
}) {
  const setSchema = useDesignerStore((s) => s.setSchema);
  const schema = useDesignerStore((s) => s.schema);

  const isControlled = value !== undefined;

  // Sync from props into internal store
  useEffect(() => {
    if (isControlled && value) {
      // only update when reference differs to reduce churn
      if (schema !== value) setSchema(value, false);
    } else if (!isControlled && defaultValue) {
      if (schema !== defaultValue) setSchema(defaultValue, false);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isControlled, value, defaultValue]);

  // Emit changes only when uncontrolled
  useEffect(() => {
    if (!isControlled && onChange) onChange(schema);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [schema, isControlled]);

  return (
    <div className="flex w-full h-full">
      <aside className="w-[260px] border-r p-3 shrink-0 overflow-auto"><LeftPanel /></aside>
      <main className="flex-1 overflow-auto p-6"><Canvas registry={registry} viewport={viewport} /></main>
      <aside className="w-[280px] border-l p-3 shrink-0 overflow-auto"><RightPanel /></aside>
      {showJSON && (
        <pre className="fixed right-0 bottom-0 w-[420px] h-[50vh] bg-white/95 p-3 overflow-auto">
          {JSON.stringify(schema, null, 2)}
        </pre>
      )}
    </div>
  );
}
